<template lang="pug">
    .groups__item-content
        .groups__item-content-container
            .groups__item-buttons
                button(@click.prevent='groupExpandEvent()' :id='group.id' v-if="!containerVisible").groups__item-expand
                button(@click.prevent='groupHideEvent()' v-if="containerVisible").groups__item-hide
            .group__number {{index+1}}
            .group__code {{group.code}}
            .group__subject {{group.subject.name}}
            .group__class {{group.groupClass}}
            .group__scholars {{group.count}}
        group-item-container(
            v-if='containerVisible'
            :group='group'
            :department='filteredDepartment'
        )
</template>    
<script>
import { mapActions } from "vuex";
export default {  
    components: {
        groupItemContainer: () => import("../../admin/groups/group-item-container")
    },
    created() {
        this.fetchDepartmentSubjects(this.group.department.id)
        this.fetchDepartmentTeachers(this.group.department.id)
    },
    data() {
        return {
            filteredDepartment:  this.departments.filter(ele=>ele.id==this.group.department.id)[0],
            containerVisible: this.groupItemContainerVisible
        }
    },
    props: {
        departments: Array,
        group: Object,
        index: Number,
        groupItemContainerVisible: Boolean
    },
    methods: {
        ...mapActions("departments", ["fetchDepartmentSubjects", "fetchDepartmentTeachers"]),
        groupExpandEvent(){
            this.containerVisible=!this.containerVisible
            let buttons = document.getElementsByClassName('groups__item-expand')
            buttons.forEach(elem => {
                elem.disabled=true
            });
            document.getElementById(this.group.id).removeAttribute('disabled')
        },
        groupHideEvent() {
            this.containerVisible=!this.containerVisible
            let buttons = document.getElementsByClassName('groups__item-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        }
    }
}
</script>
<style lang="scss">
.groups__item-content {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.groups__item-content-container {
    width: 100%;
    display: flex;
    background: #d0d2d3;
    padding: 4px 0;
    border-bottom: 1px dotted black;
    min-height: 2.5rem;
    align-items: center;
    margin-bottom: 5px;
}
.group__number {
    width: 10%;
}
.group__code, .group__subject, .group__class  {
    width: 23%; 
    vertical-align: -webkit-baseline-middle;
}
</style>